.hero {
  position: relative;
  max-height: 960px;
  height: auto;
  width: 100%;
  overflow: hidden;
  .slider {
    width: 100%;
    position: relative;
    .slider-wrapper {
      height: 960px;
      .slider-item {
        display: none;
        width: 100%;
        height: 100%;
        box-sizing: border-box;
        background-position: center center;
        background-size: cover;
      }
      .slider-item.active {
        display: block;
      }
      .slider-item:first-child {
        background-image: url("../img/glacier.png");
      }

      .slider-item:nth-child(2) {
        background-image: url("../img/lake.png");
      }

      .slider-item:nth-child(3) {
        background-image: url("../img/forest.png");
      }
    }
  }
  .btn-direction {
    position: absolute;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    z-index: 3;
    color: #ffffff;
    top: 46%;
    cursor: pointer;
    .prev,.next {
      margin: 10px;
    }
  }
  .indicators {
    position: absolute;
    width: 100%;
    text-align: center;
    justify-content: center;
    align-items: center;
    bottom: 10px;
    z-index: 3;
    .carousel-indicator{
      display: inline-block;
      width: 50px;
      padding: 1px;
      margin: 0 5px 0 5px;
      background-color: #bab8b2;
      opacity: 0.5;
    }
    .active{
      background-color:#ffffff;
      opacity: 1;
    }
  }
  .slideShow {
    position: absolute;
    bottom: 20px;
    left: 30px;
    color: #ffffff;
    font-size: 18px;
    z-index: 3;
  }
  .icon-header {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    position: absolute;
    right: 30px;
    bottom: 10px;
    text-align: center;
    height: 170px;
    color: #ffffff;
    .text {
      color: #ffffff;
    }
    a {
      border: 1px solid rgba(255, 255, 255, 0.5);
      background-color: transparent;
      color: #ffffff;
      border-radius: 50%;
      padding: 12px;
      cursor: pointer;
    }
  }
}
.info {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 20px;
  margin: 0 auto;
  width: 100%;
  height: 100%;
  text-align: center;
  color: #ffffff;
  z-index: 3;
  .hero-info {
    div img {
      height: 271px;
      width: 565px;
    }
    h2 {
      font-size: 48px;
      line-height: 62px;
      font-family: 'PT Sans', sans-serif;
      font-weight: 700;
      margin: 65px 0 0 0;
      text-transform: uppercase;
    }
    p {
      font-weight: 400;
      font-size: 48px;
      font-family: 'PT Sans', sans-serif;
      text-transform: uppercase;
      margin: 0 0 55px 0;
    }
  }
}
.btn-black,.btn-white  {
  margin: -2px;
  width: 226px;
  padding: 13px 0;
  font-family: 'PT Sans', sans-serif;
  font-size: 18px;
  font-weight: 400;
  color: #404b59;
}
.btn-white {
  background-color: #ffffff;
  border: 1px solid #ffffff;
  color: #000000;
}
.btn-black {
  background-color: transparent;
  border: 1px solid #404b59;
  color: #ffffff;
}
.item  {
  max-height: 960px;
  width: 100%;
}
.slide {
  float: left;
  position: relative;
  width: 100%;
  max-height: 960px;
  height: auto;
}
@media only screen and (max-width: 767px) {
  .hero {
    .slideShow {
      display: none;
    }
    .icon-header {
      display: none;
    }
    .indicators {
      bottom: 3px;
    }
    .slider {
      .slider-wrapper {
        height: 100vh;
        .slide {
          height: inherit;
          .info {
            height: inherit;
            .hero-info {
              div img {
                height: 100%;
                width: 75%;
              }
              h2 {
                font-size: 24px;
                line-height: 31px;
                margin: 35px 0 0 0;
              }
              p {
                font-size: 24px;
                text-transform: uppercase;
                margin: 0 0 55px 0;
              }
            }
          }
        }
      }
    }
  }
}
@media only screen and (max-width: 415px) {
  .btn-black,.btn-white  {
    margin: 5px;
  }
  .btn-black {
    background-color: #404b59;
    opacity: 0.6;
  }
  .hero {
    .btn-direction {
      top: 91%;
    }
  }
}
@media only screen and (max-width: 320px) {
  .hero {
    .btn-direction {
      top: 89%;
    }
  }
}































